input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  height: 6px;
  background: linear-gradient(to right, 
    rgba(var(--primary), 0.8),
    rgba(var(--accent), 0.8)
  );
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}
