  transform: scale(1.2);
  background: rgba(var(--primary-foreground), 1);
  box-shadow: 0 0 10px rgba(var(--primary), 0.6);
}
.bg-gradient-cool {
  background: linear-gradient(
    135deg,
    rgba(121, 40, 202, 0.2),
    rgba(50, 144, 220, 0.2)
  );
}
.bg-gradient-warm {
  background: linear-gradient(
    135deg,
    rgba(236, 72, 153, 0.2),
    rgba(234, 179, 8, 0.2)
  );
}
.glassmorphism {
  background: rgba(17, 24, 39, 0.7);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}
.glassmorphism:hover {
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.08);
}
.neon-text {
  text-shadow: 0 0 5px rgba(var(--primary), 0.7),
              0 0 10px rgba(var(--primary), 0.5),
              0 0 15px rgba(var(--primary), 0.3);
  animation: pulse 3s infinite alternate;
}
@keyframes pulse {
  0% {
    text-shadow: 0 0 5px rgba(var(--primary), 0.7),
                0 0 10px rgba(var(--primary), 0.5),
                0 0 15px rgba(var(--primary), 0.3);
  }
  100% {
    text-shadow: 0 0 7px rgba(var(--accent), 0.8),
                0 0 14px rgba(var(--accent), 0.6),
                0 0 21px rgba(var(--accent), 0.4);
  }
}
.btn-glow {
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}
.btn-glow::after {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle,
    rgba(var(--primary), 0.4) 0%,
    transparent 70%
  );
  opacity: 0;
  transition: opacity 0.6s ease;
  pointer-events: none;
  mix-blend-mode: screen;
}
.btn-glow:hover::after {
  opacity: 1;
  animation: rotate 4s linear infinite;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.player-container {
  background: rgba(17, 24, 39, 0.8);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  transition: all 0.4s ease;
}
.player-container:hover {
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.6);
  transform: translateY(-2px);
}
.theme-selector {
  background: rgba(17, 24, 39, 0.7);
  backdrop-filter: blur(16px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  transition: all 0.3s ease;
}
.theme-button {
  overflow: hidden;
  transition: all 0.3s ease;
}
.theme-button:hover {
  box-shadow: 0 5px 15px -3px rgba(0, 0, 0, 0.3);
}
.animate-pulse-slow {
  animation: pulse-slow 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
@keyframes pulse-slow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
